import { defineComponent, ref } from "vue";
export default defineComponent({
  name: "VInput",
  inheritAttrs: false,
  props: {
    label: {
      type: String,
      default: "",
    },
    modelValue: {
      default: "",
    },
    selectValue: {
      default: "",
    },
    isDisabled: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { emit }) {
    const selectValue = ref(props.selectValue) as any;
    return {
      selectValue,
    };
  },
  data() {
    return {
      value: ref(""),
    };
  },
  methods: {
    change(value: any) {
      console.log("Change",value);
      this.$emit("change", value);
    },
  },
});
