import { defineComponent, ref } from 'vue';
import LitepieDatepicker from 'litepie-datepicker';
import moment from 'moment';
import Helper from '@/globalFunctions/Helper';
export default defineComponent({
  name: 'VDatepicker',
  inheritAttrs : false,
  emits: ["apply-date-filter", "clear-date-filter"],
  components: {
    LitepieDatepicker

  },
  props : {
    autoApply : {
      default : false
    },
    isSingleDate : {
       default : false
    },
    id : {
      default : 'open-datepicker' 
   }
  },

  setup(props,{ emit }) {
    const myRef = ref(null);
    const dDate = ref([moment().startOf('year').format("DD MMM YYYY"), moment().endOf('year').format("DD MMM YYYY")]) as any;
    const active_el = ref(0);
    var isApply =  props.autoApply
    const autoApply = ref(true) ;
    autoApply.value = isApply;
    const setStartDate = ref('') as any; 
    const setEndDate = ref('') as any; 
    const chooseFilter = ref('') as any; 
    const startDate = ref([moment().startOf('year').format("DD MMM YYYY")]) as any;

    const formatter = ref({
      date: 'DD MMM YYYY',
      month: 'MMM',
    });

    window.addEventListener('click', function (e: any) {
      autoApply.value = true;
    });

    

    const customShortcuts = () => {
      return [
        {
          label: 'Today',
          atClick: () => {
            const date = new Date();
            autoApply.value = false;

            addActiveClass('Today');
           
            return [
              new Date(date.setDate(date.getDate())),
              new Date(date.setDate(date.getDate() ))
            ];
          }
        },
        {
          label: 'Last 7 Days',
          atClick: (event: any) => {
            console.log('event', event)
            const date = new Date();
          
            autoApply.value = false;
            addActiveClass('Last 7 Days');
            return [
              new Date(date.setDate(date.getDate() ) ),
              new Date(date.setDate((date.getDate() - 7) ))
            ];


          }
        },
        {
          label: 'Last 30 Days',
          atClick: () => {
            const date = new Date();
            autoApply.value = false;

            addActiveClass('Last 30 Days');

            var currentMonthDay = Helper.daysInMonth(date.getDate(), date.getFullYear());
            console.log('currentMonthDay', currentMonthDay);
            return [
              new Date(date.setDate(date.getDate())),
              new Date(date.setDate((date.getDate() + 2) - currentMonthDay))
            ];
          }
        },
        {
          label: 'This month',
          atClick: () => {
            const date = new Date();
            autoApply.value = false;

            addActiveClass('This month');
            var firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
            var lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);

            return [
              firstDay,
              lastDay
            ];
          }
        },
        {
          label: 'Last month',
          atClick: () => {
            const date = new Date();
            autoApply.value = false;

            addActiveClass('Last month');
            var firstDay = new Date(date.getFullYear(), date.getMonth() - 1, 1);
            var lastDay = new Date(date.getFullYear(), date.getMonth(), 0);
            return [
              firstDay,
              lastDay
            ];
          }
        },
        {
          label: 'Last Year',
          atClick: () => {
            const date = new Date();
            addActiveClass('Last Year');
            autoApply.value = false;

            var lastyear = new Date(new Date().getFullYear() - 1, 0, 1);
            var firstDay = (new Date(lastyear.getFullYear(), 0, 1)).getTime();
            var lastDay = (new Date(lastyear.getFullYear(), 11, 31)).getTime();
            return [
              firstDay,
              lastDay
            ];
          }
        },
        {
          label: 'Quarter 1',
          atClick: () => {
            const date = new Date();
            addActiveClass('Quarter 1');
            autoApply.value = false;

            var firstDay = moment()
              .quarter(1)
              .startOf("quarter")
              .format("YYYY-MM-DD");
            var lastDay = moment().quarter(1).endOf("quarter").format("YYYY-MM-DD");
            return [
              firstDay,
              lastDay
            ];
          }
        },

        {
          label: 'Quarter 2',
          atClick: () => {
            const date = new Date();
            addActiveClass('Quarter 2');
            autoApply.value = false;

            var firstDay = moment()
              .quarter(2)
              .startOf("quarter")
              .format("YYYY-MM-DD");
            var lastDay = moment().quarter(2).endOf("quarter").format("YYYY-MM-DD");
            return [
              firstDay,
              lastDay
            ];
          }
        },

        {
          label: 'Quarter 3',
          atClick: () => {
            const date = new Date();
            addActiveClass('Quarter 3');
            autoApply.value = false;

            var firstDay = moment()
              .quarter(3)
              .startOf("quarter")
              .format("YYYY-MM-DD");

            var lastDay = moment().quarter(3).endOf("quarter").format("YYYY-MM-DD");
            return [
              firstDay,
              lastDay
            ];
          }
        },

        {
          label: 'Quarter 4',
          atClick: () => {
            const date = new Date();
            addActiveClass('Quarter 4');
            autoApply.value = false;

            var firstDay = moment()
              .quarter(4)
              .startOf("quarter")
              .format("YYYY-MM-DD");
            var lastDay = moment().quarter(4).endOf("quarter").format("YYYY-MM-DD");
            return [
              firstDay,
              lastDay
            ];
          }
        },

        {
          label: 'This Year',
          atClick: () => {
            const date = new Date();
            autoApply.value = false;

            addActiveClass('This Year');
            var lastyear = new Date(new Date().getFullYear(), 0, 1);
            var firstDay = (new Date(lastyear.getFullYear(), 0, 1)).getTime();
            var lastDay = (new Date(lastyear.getFullYear(), 11, 31)).getTime();
            return [
              firstDay,
              lastDay
            ];
          }
        },

        {
          label: 'Next Year',
          atClick: () => {
            const date = new Date();
            autoApply.value = false;

            addActiveClass('Next Year');
            var lastyear = new Date(new Date().getFullYear() + 1, 0, 1);
            var firstDay = (new Date(lastyear.getFullYear(), 0, 1)).getTime();
            var lastDay = (new Date(lastyear.getFullYear(), 11, 31)).getTime();
            return [
              firstDay,
              lastDay
            ];
          }
        },

        {
          label: 'Apply',
          atClick: () => {
            const date = new Date(dDate.value);
            const startDate = new Date(dDate.value[0]);
            const endDate = new Date(dDate.value[1])
            dDate.value = [moment(startDate).format("DD MMM YYYY"), moment(endDate).format("DD MMM YYYY")];
            autoApply.value = true;
            console.log('dDAte',dDate);
           
            emit('apply-date-filter', { date: dDate.value, filter: chooseFilter });
            return [moment(startDate).format("DD MMM YYYY"), moment(endDate).format("DD MMM YYYY")];
          }
        },

        {
          label: 'Clear',
          atClick: () => {
            const date = new Date();
            autoApply.value = true;
            addActiveClass('Clear');
            dDate.value = [moment().startOf('year').format("DD MMM YYYY"), moment().endOf('year').format("DD MMM YYYY")]
            emit('apply-date-filter', { date: dDate.value, filter: chooseFilter });
            return [moment().startOf('year').format("DD MMM YYYY"), moment().endOf('year').format("DD MMM YYYY")];
          }
        },

      ];
    }

    const addActiveClass = async (lable: any) => {
      var buttons = document.getElementsByClassName('litepie-shortcuts');
      chooseFilter.value = lable;
      for (var i = 0; i < buttons.length; i++) {
        console.log(buttons[i].innerHTML)
        if (buttons[i].innerHTML == lable && buttons[i].innerHTML != 'Clear') {
          buttons[i].classList.add("active");
        }else{
          buttons[i].classList.remove("active");        }
      }
    }

    const returnDate = async (starDate: any, endDate : any) => {
      setStartDate.value = starDate;
      setEndDate.value = endDate;
    }


    const options = ref({
      footer: {
        id:'apply',
        apply: 'Apply',
        cancel: 'Clear'
      }
    })

    return {
      myRef,
      formatter,
      options,
      dDate,
      customShortcuts,
      active_el,
      addActiveClass,
      autoApply,
      setStartDate,
      setEndDate,
      chooseFilter,
      startDate
    };

  },
  methods: {
    selectedDate() {
      console.log('selecteddate', this.startDate); 
    },
  },

})


