import { defineComponent, ref } from "vue";
export default defineComponent({
  name: "VTextarea",
  inheritAttrs: false,
  props: {
    label: {
      type: String,
      default: "",
    },
    inputType: {
      type: [String, Number],
      default: "text",
    },
    modelValue: {
      type: [String, Number],
      default: "",
    },
    isDisabled: {
      type: Boolean,
      default: false
    },
  },
  data() {
    return {
      value: ref(""),
      description: ref(""),
    };
  },
  setup(props, { emit }) {
    const bindValue = ref('') as any;
    return {
        bindValue,
    };
  },
  methods: {
    ontext(value: any) {
      console.log('value',value)   ;
      this.$emit("textchange", value);
    },
  },
});
