/* eslint-disable @typescript-eslint/no-explicit-any */
import Helper from '@/globalFunctions/Helper'
import ServiceEngagement from '@/service/ServiceEngagement/ServiceEngagement'
import { defineAsyncComponent, defineComponent, onBeforeMount, ref } from 'vue'
const CARightMenu = defineAsyncComponent(() => import('../Dashboard/CARightMenu/CARightMenu.vue'))
const DrawerTable = defineAsyncComponent(() => import('../Dashboard/DrawerTable/DrawerTable.vue'))
const SpeakerStatusTracker = defineAsyncComponent(() => import('../Dashboard/Chart/SpeakerStatusTracker/SpeakerStatusTracker.vue'))
const EngagementByStatus = defineAsyncComponent(() => import('../Dashboard/Chart/EngagementByStatus/EngagementByStatus.vue'))

export default defineComponent({
  name: 'Dashboard',
  inheritAttrs: false,
  components: {
    'ca-right-menu': CARightMenu,
    'spk-status-tracker': SpeakerStatusTracker,
    'eng-by-status': EngagementByStatus,
    'drawer-table': DrawerTable
  },
  data () {
    return {
      show: false,
      istabledrawer: false
    }
  },
  setup () {
    const loader = ref(false)
    const seetingArray = ref([])
    const isShowTabel = ref(false)
    const selectedChartData = ref({}) as any
    const autoApply = ref(false) as any
    const chartReload = ref(true) as any
    const dateObj = ref({}) as any
    onBeforeMount(async () => {
      dateObj.value = {
        starttime: Helper.getCurrentYearDateFormat('start'),
        endtime: Helper.getCurrentYearDateFormat('end')
      }
      loader.value = true
      await DashboarSetting()
      loader.value = false
    })
    const DashboarSetting = async () => {
      const url = '/charts/settings'
      const res = await ServiceEngagement.commonEndpoint('get', url)
      if (res.apiStatus && Array.isArray(res.result) && res.result.length !== 0) {
        seetingArray.value = res.result
      }
      console.log('DashboarSetting', res)
    }
    const getTableData = async (chartValue: any) => {
      selectedChartData.value = chartValue
      console.log('Setting Tittle', selectedChartData.value.chartSetting.title)
      if (chartValue.selectedLabel) {
        isShowTabel.value = !isShowTabel.value
      }
    }
    const changeTableData = async (event: any) => {
      console.log('------ changeTableData --------', changeTableData)
      selectedChartData.value = event
    }

    return {
      loader,
      DashboarSetting,
      seetingArray,
      getTableData,
      isShowTabel,
      selectedChartData,
      changeTableData,
      autoApply,
      chartReload,
      dateObj
    }
  },

  methods: {
    logout () {
      localStorage.removeItem('token')
      this.$router.push('/login')
    },
    engagementpage () {
      this.$router.push('/engagements')
    },
    dashtable () {
      this.istabledrawer = !this.istabledrawer
    },
    // Apply Date filter
    async applyDateFilter (filterDate: any) {
      console.log('filterDate', filterDate)
      this.chartReload = false
      if (filterDate.date && filterDate.date.length !== 0) {
        if (filterDate.date[0] === filterDate.date[1]) {
          const date = new Date(filterDate.date[1])
          filterDate.date[1] = new Date(date.setDate(date.getDate() + 1))
          console.log('filterDate[1]', filterDate.date[1])
        }
        if (filterDate.filter.value === 'Last 7 Days') {
          this.dateObj.starttime = Helper.convertToUTC(
            filterDate.date[1]
          )
          this.dateObj.endtime = Helper.convertToUTC(filterDate.date[0])
        } else {
          this.dateObj.starttime = Helper.convertToUTC(
            filterDate.date[0]
          )
          this.dateObj.endtime = Helper.convertToUTC(filterDate.date[1])
        }
      }
      setTimeout(() => {
        this.chartReload = true
      }, 100)
      console.log('----- Setting Array -----', this.seetingArray)
    }
  }
})
